<template>
  <div class="cms-list-layout">
    <cms-breadcrumb to="/finance/settlement/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <cms-prompt  collapse='true'>
      <li>展示商家对结算信息有疑议的条目明细</li>
      <li>可对明细逐条或批量审核确认，如对商家有疑议的条目有疑议的建议与商家提前进行沟通与协商再进行处理</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>订单类型</label>
        <el-select v-model="queryParams.businessType" @change="query" placeholder="请选择">
          <el-option label="订单" :value="1"></el-option>
          <el-option label="售后单" :value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>异常类型</label>
        <el-select v-model="queryParams.errorType" @change="query" placeholder="请选择">
          <el-option label="金额异常" :value="1"></el-option>
          <el-option label="订单未结算" :value="2"></el-option>
          <el-option label="订单重复结算" :value="3"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>单号</label>
        <el-input v-model="queryParams.orderNumber"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <section class="cms-batch" v-if="isEdit">
      <cms-button icon="add" type="success" size="small" @click="openAddException">添加异常数据</cms-button>
      <!-- <cms-button icon="error" type="error" size="small">批量删除</cms-button> -->
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <!-- <el-table-column type="selection" width="46" fixed='left'></el-table-column> -->
        <el-table-column prop="settlement.businessNumber" label="结算批次号" width="170"></el-table-column>
        <el-table-column prop="settlement.store.storeName" label="商家" min-width="130" align="center"></el-table-column>
        <el-table-column prop="businessType" label="订单类型" min-width="100" align="center">
          <div slot-scope="scope">
            <span v-if="scope.row.businessType == 1">订单</span>
            <span v-else>售后服务单</span>
          </div>
        </el-table-column>
        <el-table-column prop="" label="单号/交易流水号" min-width="240">
          <div slot-scope="scope">
            <p>单号：{{scope.row.businessCode}}</p>
            <p>流水号：{{scope.row.order.outTradeNum}}</p>
          </div>
        </el-table-column>
        <el-table-column prop="" label="异常类型" min-width="100" align="center">
          <div slot-scope="scope">
            <span v-if="scope.row.errorType == '1'">金额异常</span>
            <span v-if="scope.row.errorType == '2'">订单未结算</span>
            <span v-if="scope.row.errorType == '3'">订单重复结算</span>
          </div>
        </el-table-column>
        <el-table-column prop="" label="创建人" width="120" align="center">
          <div slot-scope="scope">
            <span>{{scope.row.createUser + '[' + (scope.row.userType == 1?'平台':'商家') +']'}}</span>
          </div>
        </el-table-column>
        <el-table-column prop="" label="创建时间" min-width="170" align="center"> 
          <div slot-scope="scope">
            <span>{{$moment(scope.row.createTime, 'YYYY-MM-DD HH:mm:ss')}}</span>
          </div>
        </el-table-column>
        <el-table-column prop="" label="成交金额/差额" min-width="110" align="center">
          <div slot-scope="scope">
            <p>￥ <span v-if="scope.row.businessType === 1">{{$cms.float(scope.row.order.orderPayAmount)}}</span>
            <span v-else>{{$cms.float(scope.row.orderRefund.refundAmount)}}</span>
            </p>
            <p :class="scope.row.turnoverAmount >=  0?'green':'red'"> 
              <span>{{scope.row.turnoverAmount >= 0?'+':'-'}}</span> {{$cms.float(scope.row.turnoverAmount >=0?scope.row.turnoverAmount:-scope.row.turnoverAmount)}}</p>
          </div>
        </el-table-column>
        <el-table-column prop="" label="积分/差额" min-width="110" align="center">
          <div slot-scope="scope">
            <p>￥ <span v-if="scope.row.businessType === 1">{{$cms.float(scope.row.order.integralDiscount)}}</span>
            <span v-else>{{$cms.float(scope.row.orderRefund.refundIntegralAmount)}}</span>
            </p>
            <p :class="scope.row.integralDeductionAmount >=  0?'green':'red'"> 
              <span>{{scope.row.integralDeductionAmount >=  0?'+':'-'}}</span> {{$cms.float(scope.row.integralDeductionAmount >=0?scope.row.integralDeductionAmount:-scope.row.integralDeductionAmount)}}</p>
          </div>
        </el-table-column>
        <el-table-column prop="" label="红包/差额" min-width="110" align="center">
          <div slot-scope="scope">
            <p>￥ <span v-if="scope.row.businessType === 1">{{$cms.float(scope.row.order.redPacketDiscount)}}</span>
            <span v-else>{{$cms.float(scope.row.orderRefund.refundRedPacketAmount)}}</span>
            </p>
            <p :class="scope.row.redenvelopeOffset >=  0?'green':'red'"> 
              <span>{{scope.row.redenvelopeOffset >=  0?'+':'-'}}</span> {{$cms.float(scope.row.redenvelopeOffset >=0?scope.row.redenvelopeOffset:-scope.row.redenvelopeOffset)}}</p>
          </div>
        </el-table-column>
        <el-table-column prop="" label="平台优惠/差额" min-width="110" align="center">
          <div slot-scope="scope">
            <p>￥ <span v-if="scope.row.businessType === 1">{{$cms.float(scope.row.order.platformDiscount)}}</span>
            <span v-else>{{$cms.float(scope.row.orderRefund.refundPlatformDiscount)}}</span>
            </p>
            <p :class="scope.row.platformCouponOffset >=  0?'green':'red'"> 
              <span>{{scope.row.platformCouponOffset >=  0?'+':'-'}}</span> {{$cms.float(scope.row.platformCouponOffset >=0?scope.row.platformCouponOffset:-scope.row.platformCouponOffset)}}</p>
          </div>
        </el-table-column>
        
        <el-table-column v-if="isEdit" label="操作" width='160' fixed="right" align="center">
          <div slot-scope="scope">
            <cms-button icon='edit' type='info' size='small' slot="reference" @click="openException(scope.row)" >修改</cms-button>
            <cms-button icon='error' type='info' size='small' slot="reference" @click="deleteData(scope.row.id)" >删除</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      <el-dialog
        title="异常数据修改"
        :visible.sync="exceptionHand"
        width="700px"
        :before-close="done">
       <div class="add-exception">
         <div class="cms-prompt" ref="prompt" id="prompt">
            <div class="cms-prompt-icon">
              <img src="../../../assets/images/icon-prompt.png" alt="">
              <span>温馨提示:</span> 
            </div>
            <ul class="cms-prompt-info">
              <li>避免后期争议，平台填写的异常数据须由平台和商家确认</li>
            </ul>
          </div>
          <el-form ref="form" :rules="rules" :model="formPrams" label-width="160px">
            <el-form-item label="订单类型：">
              <span>{{formPrams.settlementError.businessType == 1?'订单':'售后服务单'}}</span>
            </el-form-item>
            <el-form-item label="订单：">
              <span>{{formPrams.settlementError.businessCode}}</span>
              <!-- <el-input v-model="formData.Code"></el-input> -->
            </el-form-item>
            <el-form-item label="异常类型：">
              <span v-if="formPrams.settlementError.errorType == 1">金额异常</span>
              <span v-if="formPrams.settlementError.errorType == 2">订单未结算</span>
              <span v-if="formPrams.settlementError.errorType == 3">订单重复结算</span>
            </el-form-item>
            <el-form-item label="原因：">
              <span style="word-wrap: break-word;">{{formPrams.settlementError.reason}}</span>
            </el-form-item>
            <el-form-item label="成交金额：" prop="settlementError.turnoverAmount">
              <span class="add-exception__price">￥ {{$cms.float(formData.orderPayAmount)}}</span><span class="t-add">{{formData.turnoverAmount >=0?'+':'-'}}</span>
              <el-input maxlength="6" placeholder="0.00" v-model="formPrams.settlementError.turnoverAmount"></el-input>
            </el-form-item>
            <el-form-item label="积分：" prop="settlementError.integralDeductionAmount">
              <span class="add-exception__price">￥ {{$cms.float(formData.useIntegral)}}</span><span class="t-add">{{formData.integralDeductionAmount >=0?'+':'-'}}</span>
              <el-input  maxlength="6" placeholder="0.00" v-model="formPrams.settlementError.integralDeductionAmount"></el-input>
            </el-form-item>
            <el-form-item label="红包：" prop="settlementError.redenvelopeOffset">
              <span class="add-exception__price">￥ {{$cms.float(formData.useRedPacket)}}</span><span class="t-add">{{formData.redenvelopeOffset >=0?'+':'-'}}</span>
              <el-input  maxlength="6" placeholder="0.00" v-model="formPrams.settlementError.redenvelopeOffset"></el-input>
            </el-form-item>
            <el-form-item label="平台优惠：" prop="settlementError.platformCouponOffset">
              <span class="add-exception__price">￥ {{$cms.float(formData.platformDiscount)}}</span><span class="t-add">{{formData.platformCouponOffset >=0?'+':'-'}}</span>
              <el-input  maxlength="6" placeholder="0.00" v-model="formPrams.settlementError.platformCouponOffset"></el-input>
            </el-form-item>
            <el-form-item label="说明：">
              <el-input type="textarea" rows="4" maxlength="200" resize="none" placeholder="请填写200字以内的修改说明"
              v-model="formPrams.settlementErrorDetail.operateTxt"></el-input>
            </el-form-item>
          </el-form>
       </div>
        <span slot="footer" class="dialog-footer">
          <el-button size="mini" @click="done">取 消</el-button>
          <el-button size="mini" type="primary" @click="updateException">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog
        title="异常数据添加"
        :visible.sync="addExceptionHand"
        width="700px"
        :before-close="done2">
       <div class="add-exception">
         <div class="cms-prompt" ref="prompt" id="prompt">
            <div class="cms-prompt-icon">
              <img src="../../../assets/images/icon-prompt.png" alt="">
              <span>温馨提示:</span>
            </div>
            <ul class="cms-prompt-info">
              <li>避免后期争议，平台填写的异常数据须由平台和商家确认</li>
            </ul>
          </div>
          <el-form ref="form2" :rules="rules" :model="formPrams" label-width="160px">
            <el-form-item label="订单类型：" prop="settlementError.businessType">
              <el-radio-group v-model="formPrams.settlementError.businessType">
                <el-radio :label="1">订单</el-radio>
                <el-radio :label="2">售后服务单</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="订单：" prop="settlementError.businessCode">
              <el-select
                v-model="formPrams.settlementError.businessCode"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="getOrder"
                @change="getItem"
                :loading="loading">
                <el-option
                  v-for="(item,index) in selOrder"
                  :key="index"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
              <div v-if="showError" class="el-form-item__error" style="top:5px;">已存在订单异常</div>
            </el-form-item>
            <el-form-item label="异常类型：">
              <el-select v-model="formPrams.settlementError.errorType" placeholder="请选择">
                <el-option label="金额异常" :value="1"></el-option>
                <el-option label="订单未结算" :value="2"></el-option>
                <el-option label="订单重复结算" :value="3"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="原因：">
              <el-input type="textarea" rows="4" maxlength="200" resize="none" placeholder="请填写200字以内的修改说明"
              v-model="formPrams.settlementError.reason"></el-input>
            </el-form-item>
            <div v-show="showAmount">
              <el-form-item label="成交金额：" prop="settlementError.turnoverAmount">
              <span class="add-exception__price">￥ {{$cms.float(formData.orderPayAmount)}}</span>
              <el-input  maxlength="6" placeholder="0.00" :max="formData.orderPayAmount" v-model="formPrams.settlementError.turnoverAmount">
                <el-select v-model="select01" slot="prepend" placeholder="请选择">
                  <el-option label="+" :value="1"></el-option>
                  <el-option label="-" :value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            <el-form-item label="积分：" prop="settlementError.integralDeductionAmount">
              <span class="add-exception__price">￥ {{$cms.float(formData.useIntegral)}}</span>
              <el-input  maxlength="6" placeholder="0.00" :max="formData.useIntegral" v-model="formPrams.settlementError.integralDeductionAmount">
                <el-select v-model="select02" slot="prepend" placeholder="请选择">
                  <el-option label="+" :value="1"></el-option>
                  <el-option label="-" :value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            <el-form-item label="红包：" prop="settlementError.redenvelopeOffset">
              <span class="add-exception__price">￥ {{$cms.float(formData.useRedPacket)}}</span>
              <el-input  maxlength="6" placeholder="0.00" :max="formData.useRedPacket" v-model="formPrams.settlementError.redenvelopeOffset">
                <el-select v-model="select03" slot="prepend" placeholder="请选择">
                  <el-option label="+" :value="1"></el-option>
                  <el-option label="-" :value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            <el-form-item label="平台优惠：" prop="settlementError.platformCouponOffset">
              <span class="add-exception__price">￥ {{$cms.float(formData.platformDiscount)}}</span>
              <el-input  maxlength="6" placeholder="0.00" :max="formData.platformDiscount" v-model="formPrams.settlementError.platformCouponOffset">
                <el-select v-model="select04" slot="prepend" placeholder="请选择">
                  <el-option label="+" :value="1"></el-option>
                  <el-option label="-" :value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
            </div>
          </el-form>
       </div>
        <span slot="footer" class="dialog-footer">
          <el-button size="mini" @click="done2">取 消</el-button>
          <el-button size="mini" type="primary" @click="saveException">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import { storesettlementerrorPage,
  checkBusinessNumber,
  storesettlementerrorUpdate,
  storesettlementerrorDelete,
  storesettlementerrorGet,
  storesettlementerrorSave,
  getOrderNumber } from '@/api/finance/finance.js'
import list from '@/mixins/list'
import form from '@/mixins/form'
import rules from '@/rules/index.js'
var defaultData = {
  'page': 1,
  'size': 10,
  'settlementId': '',
  'errorType': '',
  'businessType': '',
  'orderNumber': ''
}
var defaultForm = {
  'settlementError': {
    'id': '',
    'businessCode': '',
    'businessType': 1,
    'errorType': 1,
    'reason': '',
    'turnoverAmount': 0,
    'integralDeductionAmount': 0,
    'redenvelopeOffset': 0,
    'platformCouponOffset': 0
  },
  'settlementErrorDetail': {
    'operateTxt': ''
  }
}
export default {
  mixins: [list, form],
  name: 'financeAbnomal',
  data() {
    return {
      queryParams: Object.assign({}, defaultData), // 异常列表数据
      formPrams: Object.assign({}, defaultForm), // 异常-提交数据
      isEdit: false, // 是否是修改
      loading: false, // 加载
      exceptionHand: false, // 添加弹窗显示
      addExceptionHand: false, // 修改弹窗显示
      showError: false, // 订单异常是否存在-错误提示
      rules: {
        'settlementError.turnoverAmount': [rules.required(), rules.decimal()],
        'settlementError.integralDeductionAmount': [rules.required(), rules.decimal()],
        'settlementError.redenvelopeOffset': [rules.required(), rules.decimal()],
        'settlementError.platformCouponOffset': [rules.required(), rules.decimal()],
        'settlementError.businessCode': [rules.required(), rules.decimal()],
        'settlementError.businessType': [rules.required(), rules.decimal()]
      },
      select01: 1,
      select02: 1,
      select03: 1,
      select04: 1,
      formData: {},
      showAmount: false,
      selOrder: []
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(storesettlementerrorDelete, id) // 这个才是真实的删除方法
    },
    done(type) { // 关闭修改弹窗
      this.$refs.form.resetFields()
      this.formPrams = Object.assign({}, defaultForm)
      this.formData = {}
      this.exceptionHand = false
    },
    done2() { // 关闭新增弹窗
      this.$refs.form2.resetFields()
      this.formPrams = Object.assign({}, defaultForm)
      this.showAmount = false
      this.formData = {}
      this.select01 = 1
      this.select02 = 1
      this.select03 = 1
      this.select04 = 1
      this.addExceptionHand = false
      this.showError = false
    },
    openException(data) { // 打开修改弹窗
      storesettlementerrorGet({ id: data.id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
          const obj = {
            'settlementError': {
              'id': res.data.id,
              'businessCode': res.data.businessCode,
              'businessType': res.data.businessType,
              'errorType': res.data.errorType,
              'reason': res.data.reason,
              'turnoverAmount': res.data.turnoverAmount >= 0 ? res.data.turnoverAmount : -res.data.turnoverAmount,
              'integralDeductionAmount': res.data.integralDeductionAmount >= 0 ? res.data.integralDeductionAmount : -res.data.integralDeductionAmount,
              'redenvelopeOffset': res.data.redenvelopeOffset >= 0 ? res.data.redenvelopeOffset : -res.data.redenvelopeOffset,
              'platformCouponOffset': res.data.platformCouponOffset >= 0 ? res.data.platformCouponOffset : -res.data.platformCouponOffset
            },
            'settlementErrorDetail': {
              'operateTxt': ''
            }
          }
          this.formPrams = obj
          if (res.data.businessType === 1) { // 当前订单类型
            this.formData.orderPayAmount = res.data.order.orderPayAmount
            this.formData.useIntegral = res.data.order.integralDiscount
            this.formData.useRedPacket = res.data.order.redPacketDiscount
            this.formData.platformDiscount = res.data.order.platformDiscount
          } else { // 售后类型
            this.formData.orderPayAmount = res.data.orderRefund.refundAmount
            this.formData.useIntegral = res.data.orderRefund.refundIntegralAmount
            this.formData.useRedPacket = res.data.orderRefund.refundRedPacketAmount
            this.formData.platformDiscount = res.data.orderRefund.refundPlatformDiscount
          }
          this.exceptionHand = true
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    openAddException() { // 打开新增异常弹窗
      defaultForm = {
        'settlementError': {
          'id': '',
          'businessCode': '',
          'businessType': 1,
          'errorType': 1,
          'reason': '',
          'turnoverAmount': 0,
          'integralDeductionAmount': 0,
          'redenvelopeOffset': 0,
          'platformCouponOffset': 0
        },
        'settlementErrorDetail': {
          'operateTxt': ''
        }
      }
      this.formPrams = Object.assign({}, defaultForm)
      this.addExceptionHand = true
    },
    getOrder(val) { // 获取订单号
      if (val !== '' && val.length > 2) {
        const params = {
          businessType: this.formPrams.settlementError.businessType,
          businessNumber: val,
          storeId: this.$route.query.storeId
        }
        getOrderNumber(params).then(res => {
          if (res.code === 200) {
            this.selOrder = res.data
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        this.showAmount = false
      }
    },
    getItem(val) { // 获取订单号
      if (val && val !== '') {
        const params = {
          businessType: this.formPrams.settlementError.businessType,
          businessCode: val,
          storeId: this.$route.query.storeId
        }
        checkBusinessNumber(params).then(res => {
          if (res.code === 200) {
            this.formData = res.data
            this.formPrams.settlementError.settlementId = this.$route.query.sid
            if (params.businessType === 1) { // 当前订单类型
              this.formData.orderPayAmount = res.data.orderPayAmount
              this.formData.useIntegral = res.data.integralDiscount
              this.formData.useRedPacket = res.data.redPacketDiscount
              this.formData.platformDiscount = res.data.platformDiscount
            } else { // 售后类型
              this.formData.orderPayAmount = res.data.refundAmount
              this.formData.useIntegral = res.data.refundIntegralAmount
              this.formData.useRedPacket = res.data.refundRedPacketAmount
              this.formData.platformDiscount = res.data.refundPlatformDiscount
            }
            this.showAmount = true
          } else {
            this.showError = true
          }
        })
      }
    },
    updateException() { // 修改异常弹窗
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          if (this.formData.turnoverAmount < 0) {
            this.formPrams.settlementError.turnoverAmount = '-' + this.formPrams.settlementError.turnoverAmount
          }
          if (this.formData.integralDeductionAmount < 0) {
            this.formPrams.settlementError.integralDeductionAmount = '-' + this.formPrams.settlementError.integralDeductionAmount
          }
          if (this.formData.redenvelopeOffset < 0) {
            this.formPrams.settlementError.redenvelopeOffset = '-' + this.formPrams.settlementError.redenvelopeOffset
          }
          if (this.formData.platformCouponOffset < 0) {
            this.formPrams.settlementError.platformCouponOffset = '-' + this.formPrams.settlementError.platformCouponOffset
          }
          storesettlementerrorUpdate(this.formPrams).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
              this.done()
              this.query()
            } else {
              this.$errorMessage(res.message)
              this.done()
            }
          })
        }
      })
    },
    saveException() { // 保存新增异常
      if (!this.showError) {
        this.$refs.form2.validate((valid, item) => {
          if (valid) {
            delete this.formPrams.settlementError.id
            delete this.formPrams.settlementErrorDetail
            if (this.select01 === 2) {
              this.formPrams.settlementError.turnoverAmount = '-' + this.formPrams.settlementError.turnoverAmount
            }
            if (this.select02 === 2) {
              this.formPrams.settlementError.integralDeductionAmount = '-' + this.formPrams.settlementError.integralDeductionAmount
            }
            if (this.select03 === 2) {
              this.formPrams.settlementError.redenvelopeOffset = '-' + this.formPrams.settlementError.redenvelopeOffset
            }
            if (this.select04 === 2) {
              this.formPrams.settlementError.platformCouponOffset = '-' + this.formPrams.settlementError.platformCouponOffset
            }
            storesettlementerrorSave(this.formPrams).then(res => {
              if (res.code === 200) {
                this.$successMessage('添加成功')
                this.done2()
                this.query()
              } else {
                this.$errorMessage(res.message)
                this.done2()
              }
            })
          }
        })
      }
    }
  },
  created() {
    // 获取结算周期id
    this.queryParams.settlementId = this.$route.query.id
    if (this.$route.query.isEdit) { // 是否可以进行操作
      this.isEdit = this.$route.query.isEdit
    }
    this.getTableData(storesettlementerrorPage, this.queryParams)
  }
}
</script>


<style lang="scss" scoped>
 .el-input .el-select {
    width: 55px !important;
  }
.add-exception{
  padding: 10px;
  &__price{
    display: inline-block;
    width: 100px;
    font-size: #666;
  }
}
.cms-layout .add-exception .el-form-item__content > .el-input{
  width: 180px;
}
 .cms-layout .el-form-item__content .el-cascader{
  width: 30px;
}
.cms-prompt{
    cursor: pointer;
    width: 100%;
    background-color: #f4f8fb;
     border: dotted 1px #e0e0e0;
     padding: 0 10px;
     overflow: hidden;
     position: relative;
     transition:all .15s;
  }
  .cms-prompt-icon{
    font-size: 12px;
    color: #6798bd;
    display: flex;
    align-items:center;
    float: left;
    img{
       position: relative;
       top:2px;
       margin-right: 5px;
    }
    span{
      display:inline-block;
      margin-top: 11px;
    }
  }
  .cms-prompt-info{
    float: left;
    li{   
      font-size: 12px;
      letter-spacing: 0px;
      color: #999999;
      margin-left: 13px;
      margin-bottom: 5px;
      &:first-child{
        margin-top: 11px;
      }
    }
  }
  .t-add{
    display: inline-block;
    min-width: 8px;
  }
  .green{
    color: green;
  }
  .red{
    color: red;
  }
</style>


